.profile-pic {
  display: block;
  width: 12.500em;
  height: 12.500em;
  margin-left: auto;
  margin-right: auto;
  border-radius: 50%;
}

.block {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.custom-transparent {
  background-color: rgba(255,255,255,0.9);
}

.icons-social {
  font-size: 3em;
}

.icons-social i {
  color: black;
	padding: 10px;
}

.project-icon {
  font-size: 3em;
  color: black;
  padding: 10px;
} 

.card .card-action a:not(.btn):not(.btn-large):not(.btn-small):not(.btn-large):not(.btn-floating) {
  color: #039be5 !important;
}
